import React from "react";
import PropTypes from "prop-types";
import "./ContactForm.scss";
import { Formik, FastField, Form } from "formik";
import Input from "../Input/Input";
import Textarea from "./../Textarea/Textarea";

ContactForm.propTypes = {};

function ContactForm(props) {
  const initialValues = {
    name: "",
    address: "",
    email: "",
    phone: "",
    title: "",
    description: "",
  };
  return (
    <div className="ContactForm">
      <h3>GI THNG TIN LIN H</h3>
      <span>
        Xin vui lng in cc yu cu vo mu di y v gi cho chng ti.
        Chng ti s tr li bn ngay sau khi nhn c. Xin chn thnh cm n!
      </span>
      <Formik initialValues={initialValues}>
        <Form>
          <FastField
          className="ContainerField"
            name="name"
            component={Input}
            iconLeft="user"
            type="text"
            placeholder="H tn"
          />
          <FastField
            name="address"
            component={Input}
            iconLeft="map-marker-alt"
            type="text"
            placeholder="a ch"
          />
          <FastField
            name="email"
            component={Input}
            iconLeft="envelope"
            type="text"
            placeholder="Email"
          />
          <FastField
            name="phone"
            component={Input}
            iconLeft="phone-alt"
            type="text"
            placeholder="in thoi"
          />
          <FastField
            name="title"
            component={Input}
            iconLeft="list-alt"
            type="text"
            placeholder="Tiu "
          />
          <FastField
            name="description"
            component={Textarea}
            rows={5}
            placeholder="Ni dung"
          />
          <div className="ButtonSend">
            <button type="submit">
              Gi
            </button>
          </div>
        </Form>
      </Formik>
    </div>
  );
}

export default ContactForm;
