import React from "react";
import Table from "../Table/Table";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Button } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import "./Cart.scss";
import {
  calculateAmount,
  calculateIntoMoney,
  calculateTotal,
  formatCurrency,
} from "../../util/util";
import { ACTION_TYPE_TABLE } from "../../Constant/Constant";
import { removeToCart } from "../../actions/cart";
function Cart() {
  let cartList = useSelector((carts) => carts.cart.cartList);
  const disPatch = useDispatch();
  cartList = cartList.length
    ? cartList
    : sessionStorage.getItem("item_cart")
    ? JSON.parse(sessionStorage.getItem("item_cart")).cartList
    : [];
  cartList = calculateIntoMoney(cartList);

  const removeItemCart = (item, actionName) => {
    if (actionName === ACTION_TYPE_TABLE.DELETE) {
      disPatch(removeToCart(item));
    }
  };
  return (
    <div className="Cart">
      <div className="Image">
        <img src={require("../../assets/images/cart.png").default} />
        <span className="InfoCart">
          {calculateAmount(cartList)} sp -{" "}
          {formatCurrency(calculateTotal(cartList))} <sup></sup>
        </span>
        <FontAwesomeIcon icon="arrow-right" className="Icon" />
      </div>

      {cartList.length ? (
        <div className="InfoDetail">
          <div>
            <Table
              dataList={cartList}
              headerList={headerList}
              hanldeAction={removeItemCart}
            />
          </div>

          <div className="Total">
            <span className="Label">Tng tin:</span>
            <span className="Content">
              {formatCurrency(calculateTotal(cartList))}
              <sup></sup>
            </span>
          </div>
          <div className="ContainerButton">
            <Button className="ButtonPayment">Thanh ton</Button>
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
}

export default Cart;

const headerList = [
  {
    flex: 1,
    label: "",
    propertyMapping: "imageUrl",
    type: "IMAGE",
    formatCurrency: false,
  },
  {
    flex: 3,
    label: "TN SN PHM",
    propertyMapping: "productName",
    type: "LABLE",
    formatCurrency: false,
  },
  {
    flex: 2,
    label: "GI",
    propertyMapping: "price",
    type: "LABLE",
    formatCurrency: true,
  },
  {
    flex: 2,
    label: "S LNG",
    propertyMapping: "amount",
    type: "LABLE",
    formatCurrency: false,
  },
  {
    flex: 2,
    label: "THNH TIN",
    propertyMapping: "intoMoney",
    type: "LABLE",
    formatCurrency: true,
  },
  {
    flex: 1,
    label: "",
    actionType: ["DELETE"],
    type: "ACTION",
  },
];
