import React from "react";
import VerticalMenu from "../../component/VerticalMenu/VerticalMenu";
import NavigateQuickly from "../../component/NavigateQuickly/NavigateQuickly";
import Header from "../../component/Header/Header";
import PropTypes from "prop-types";
import "./About.scss";
import { addQuickLink, removeQuickLink } from "../../actions/quicklink";
import { useDispatch } from 'react-redux';
About.propTypes = {};

function About() {
  const disPatch = useDispatch();
  function selectedCategory(item) {
    disPatch(removeQuickLink(1));
    disPatch(addQuickLink({ path: `/about`, label: "Gii thiu" }));
    disPatch(addQuickLink({ path: `/about/${item.id}`, label: item.title }));
  }
  return (
    <div className="ContainerBody">
      <div className="ContainerBodyLeft">
        <div className="ContainerChildLeft">
          <VerticalMenu
            iconLeft="angle-double-right"
            categoryList={dataSource}
            title="Gii thiu"
            selectedCategory={selectedCategory}
          />
        </div>
      </div>
      <div className="ContainerBodyRight">
        <div className="ContainerChildRight">
          <NavigateQuickly />
        </div>
        <div className="ContainerChildRight">
          <Header title="V Chng ti" />
        </div>
        <div className="ContainerChildRight">
          <div className="Content">
            <p>
              Cng ty C phn T vn cng ngh, thit b v Kim nh xy dng -
              CONINCO l mt doanh nghip t vn u t v xy dng, hot ng
              a lnh vc, a ngnh ngh trong phm vi c nc, hp tc kinh
              doanh hiu qu vi nhiu Cng ty v tp on kinh t ln trn th
              gii.
            </p>
            <p>
              Cng ty C phn T vn cng ngh, thit b v Kim nh xy dng -
              CONINCO l mt doanh nghip t vn u t v xy dng, hot ng
              a lnh vc, a ngnh ngh trong phm vi c nc, hp tc kinh
              doanh hiu qu vi nhiu Cng ty v tp on kinh t ln trn th
              gii.
            </p>
            <p>
              T ngy thnh lp 16 thng 4 nm 1979 n nay, tri qua hn 30 nm
              xy dng v trng thnh, tp th lnh o, cn b cng nhn vin
              Cng ty  n lc khng ngng xy dng v pht trin. CONINCO 
              c ng v Nh nc ghi nhn thnh tch v tng thng nhiu
              danh hiu cao qu, gp phn to dng thng hiu CONINCO l mt
              trong nhng Cng ty hng u trong Tp on T vn Xy dng Vit
              Nam.
            </p>
            <p>
              CONINCO cng c cc Ch u t v khch hng nh gi l mt
              thng hiu T vn Xy dng ln c uy tn, tin cy, c th c lp
              m ng thc hin cng tc t vn cho cc d n quy m ln, phc
              tp v cng ngh, k thut.
            </p>
            <p>
              Hin nay, vi m hnh hot ng mi l mt Cng ty i chng, sau
              khi thc hin thnh cng v hiu qu m hnh Nhng quyn thng
              mi cho cc Cng ty thnh vin, cng vi s quan tm, gip  v
              ch o kp thi ca Lnh o cc cp, cc ngnh, s tin cy ca
              Qu khch hng, thng hiu CONINCO ngy cng pht trin v ln
              mnh.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default About;

const dataSource = [
  {
    id: "1",
    title: "V chng ti",
  },
  {
    id: "2",
    title: "Lnh vc hot ng",
  },
];
