import React, { useState } from "react";
import Steper from "../../component/Steper/Steper";
import Table from "../../component/Table/Table";
import { ACTION_TYPE_TABLE } from "../../Constant/Constant";
import NavigateQuickly from "../../component/NavigateQuickly/NavigateQuickly";
import Header from "../../component/Header/Header";
import Payment from "../../component/Payment/Payment";
import CompletedOrder from "../../component/CompletedOrder/CompletedOrder";

import { Button } from "@material-ui/core";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useRouteMatch,
  useHistory,
  Link
} from "react-router-dom";
import "./CartDetail.scss";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import {
  calculateIntoMoney,
  calculateTotal,
  formatCurrency,
} from "../../util/util";
import { removeToCart, updateAmountToCart } from "../../actions/cart";
CartDetail.propTypes = {};

function CartDetail(props) {
  const [listSteper, setListSteper] = useState(dataSourceSteper);
  let { path, url } = useRouteMatch(); //get root path
  const history = useHistory();
  let cartList = useSelector((carts) => carts.cart.cartList);
  const disPatch = useDispatch();
  cartList = cartList.length
    ? cartList
    : sessionStorage.getItem("item_cart")
    ? JSON.parse(sessionStorage.getItem("item_cart")).cartList
    : [];
  cartList = calculateIntoMoney(cartList);

  const removeItemCart = (item, actionName) => {
    if (actionName === ACTION_TYPE_TABLE.DELETE) {
      disPatch(removeToCart(item));
    }
  };

  const hanldeCalculateValue = (newValue, item, propertyName) => {
    item[propertyName] = newValue;
    disPatch(updateAmountToCart(item));
  };

  return (
    <div className="CartDetail">
      <div className="ContainerBody">
        <div className="NavQuickly">
          <NavigateQuickly />
        </div>
        <div className="Header">
          <Header title="Thanh ton" />
        </div>
        <div className="Steper">
          <Steper listSteper={listSteper} />
        </div>

        <Router>
          <Switch>
            <Route path="/cart-detail" exact={true}>
              <div className="ProductInfo">
                <Table
                  dataList={cartList}
                  headerList={headerTable}
                  hanldeCalculateValue={hanldeCalculateValue}
                  hanldeAction={removeItemCart}
                />
                <div className="Total">
                  <span className="Label">Tng thanh ton:</span>
                  <span className="Content">
                    {formatCurrency(calculateTotal(cartList))} <sup></sup>
                  </span>
                </div>
                <div className="ButtonSend">
                  <Button
                    className="ButtonLeft"
                    onClick={() => history.goBack()}
                  >
                    Tip tc mua hng
                  </Button>
                  <Button className="ButtonRight">
                    <Link to={`${path}/payment`} className="Link">Tin hnh thanh ton</Link>
                  </Button>
                </div>
              </div>
            </Route>
            <Route path={`${path}/payment`}>
              <Payment />
            </Route>
            <Route path={`${path}/completed`}>
              <CompletedOrder />
            </Route>
          </Switch>
        </Router>
      </div>
    </div>
  );
}

export default CartDetail;

let dataSourceSteper = [
  {
    isActive: true,
    iconName: "angle-double-down",
    title: "Gi hng",
  },
  {
    isActive: false,
    iconName: "list",
    title: "Thanh ton",
  },
  {
    isActive: false,
    iconName: "list",
    title: "Hon tt",
  },
];

const headerTable = [
  {
    flex: 1,
    label: "",
    propertyMapping: "imageUrl",
    type: "IMAGE",
  },
  {
    flex: 5,
    label: "TN SN PHM",
    propertyMapping: "productName",
    type: "LABLE",
    formatCurrency: false,
  },
  {
    flex: 2,
    label: "GI",
    propertyMapping: "price",
    type: "LABLE",
    formatCurrency: true,
  },
  {
    flex: 2,
    label: "GIM GI (%)",
    propertyMapping: "discount",
    type: "LABLE",
    formatCurrency: false,
  },
  {
    flex: 2,
    label: "S LNG",
    propertyMapping: "amount",
    type: "INPUTNUMBER",
    min: 1,
    max: 10,
    formatCurrency: false,
  },
  {
    flex: 2,
    label: "THNH TIN",
    propertyMapping: "intoMoney",
    type: "LABLE",
    formatCurrency: true,
  },
  {
    flex: 1,
    label: "",
    actionType: ["DELETE"],
    type: "ACTION",
  },
];
