export const addToCart= (cart)=>{
  return {
    type: "ADD_CART",
    payload: cart,
  };
}
export const updateAmountToCart = (cart) => {
  return {
    type: "UPDATE_AMOUNT_CART",
    payload: cart,
  };
};
export const removeToCart = (cart) => {
  return {
    type: "REMOVE_CART",
    payload: cart,
  };
};